import { WorkflowProvider } from './../../../providers/workflow-provider';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

/**
 * Generated class for the TestPdfPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-test-pdf',
  templateUrl: 'test-pdf.html',
})
export class TestPdfPage {

  weburl = 'http://www.popochiu.com/file/base64/getpdf'
  constructor(public navCtrl: NavController, public navParams: NavParams,
    public wfprovider: WorkflowProvider) {
  }

  ionViewDidLoad() {
  }

  fromWorkflow(pid: number) {
    this.navCtrl.push('PdfjsViewerPage', {
      pid: pid,
      title: 'pid' + pid
    })
  }

  getWebPDF() {
    this.wfprovider.GetTmpProcessPDF(this.weburl).subscribe(res => {
      let data = res.result.base64Str; 
      this.navCtrl.push('PdfjsViewerPage', {
        base64str: data,
        title: 'web pdf'
      })
    })
  }
}



